import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import Grid from 'material-ui/Grid';
import { fadeInDown, slideInDown, slideOutUp } from 'react-animations';
import Headroom from 'react-headroom';
import * as Scroll from 'react-scroll';
import { Link, DirectLink, Element , Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';

const fadeInAnimation = keyframes`${fadeInDown}`;
const slideInDownAnimation = keyframes`${slideInDown}`;
const slideOutUpAnimation = keyframes`${slideOutUp}`;

const FadeInDiv = styled.nav`
  font-family: 'Work Sans', sans-serif;
  position: fixed;
  width: 100vw;
  background-color: #212121;
  font-size: 1em;
  transform: translateY(-100px);
  animation-duration: 1s;
  animation-delay: 8s;
  animation-fill-mode: forwards;
  animation-name: ${fadeInAnimation};

  @media (min-width: 1400px) {
    font-size: 16px;
  }
`;

const NavLink = styled.a`
  text-decoration: unset;
  color: #888;
  margin: 0 30px;
`;

let c = 0;
let currentScrollTop = 0;

class Nav extends Component {
  constructor() {
    super();
    this.state = {
      scrollingDown: false,
    };
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    console.log(this.state.scrollingDown);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll() {
    console.log(window.scrollY);
    let a = window.scrollY;
    let navbar = document.getElementsByTagName('nav');
    let b = navbar[0].clientHeight;
    currentScrollTop = a;

    if(c < currentScrollTop) {
      console.log('scrolling down');
      this.setState(() => {
        return { scrollingDown: true };
      });
    }
    else if(c > currentScrollTop) {
      console.log('scrolling up');
      this.setState(() => {
        return { scrollingDown: false };
      });
    }
    c = currentScrollTop;
    console.log(this.state.scrollingDown);
  }

  render() {
    return(
      <FadeInDiv scrollDownState={this.state.scrollingDown}>
        <Grid container spacing={0} justify="flex-end" alignItems="center" style={{ backgroundColor: '#212121', paddingRight: '6vw', height: 80 }}>
          <Grid>
            <Link to="home" spy={true} hashspy={true} smooth={true} offset={-10} duration={500}><NavLink href="#">HOME</NavLink></Link>
          </Grid>
          <Grid>
            <Link to="about" spy={true} hashspy={true} smooth={true} offset={-10} duration={500}><NavLink href="#">ABOUT</NavLink></Link>
          </Grid>
        </Grid>
      </FadeInDiv>
    );
  }
}


export default Nav;



